<script>
const vscode = acquireVsCodeApi();
export default {
    name: 'QueryGenerator',
    data() {
        return {
            queryLanguage: '',
            queryPrompt: '',
        }
    },
    methods: {
        generateQuery(){  
            if(!this.queryLanguage.length){
                thsi.showError("Please enter target language");
            } 
            if(!this.queryPrompt.length){
                this.showError("Please enter a prompt");
            } 
            vscode.postMessage({ type: 'getQuery', language: this.queryLanguage,  prompt: this.queryPrompt});
        },
        showError(){
            vscode.postMessage({ type: 'showError', value: error });
        }
    }

}
</script>

